import axios from 'axios';
import { message } from 'antd';
import { getToken } from "@/App/utils/operatorLocalStorage";
import { useCallback, useState } from 'react';
import { useGlobalStore } from "@/App/store/GlobalStore";
import { useHistory } from 'react-router-dom';

const instance = axios.create({    //axios
  timeout: 3000,
  // baseURL: process.env.NODE_ENV === 'production' ? '' : '/api',   //baeUrl
});
// post
instance.defaults.headers.post['Content-Type'] = 'application/json';
// delete 
// instance.defaults.headers.delete['Content-Type'] = 'application/x-www-form-urlencoded';

let httpCode: { [propName: number]: string } = {        // http
  400: '',
  401: ', ',
  403: '',
  404: '',
  500: '',
  501: '',
  502: '',
  504: ''
};

export const useRequest = (url: string, config: any) => {
  const [loading, setLoading] = useState(false);
  const globalStore = useGlobalStore();
  const router = useHistory();
  const request = useCallback(async (params?: any, pathParams?: string | number) => {
    const requestUrl = pathParams ? `${url}/${pathParams}` : url;
    const authorization = getToken();
    if (authorization) {
      /*
        
        request.headers['Authorization']
        Authorization,401
        - request.headers.Authorizationrequest.headers.authorization
        401
        res.headers.authorizationundefined
      */
      axios.defaults.headers['Authorization'] = authorization;
    } else {
      delete axios.defaults.headers['Authorization'];
    }
    //5000netclound
    // axios.defaults.withCredentials = true
    setLoading(true);
    let response;
    try {
      console.log(requestUrl, params)
      if (config.method === 'get') {
        response = await axios({
          url: requestUrl,
          params,
          ...config
        });

      } else {
        response = await axios({
          url: requestUrl,
          data: params,
          ...config
        });
      }
      return response;
    } catch (error) {
      if (error.response) {
        // http
        let tips = error.response.status in httpCode ? httpCode[error.response.status] : error.response.data.message;
        message.error(tips);
        if (error.response.status === 401) {
          globalStore.logout();
          router.push('/login');
        }
        return Promise.reject(tips);
      } else {
        message.error('');
        return Promise.reject('');
      }
    } finally {
      setLoading(false);
    }

  }, [url, config]);
  return { request, loading };
};
