import React from 'react';
import {Form, Input, Button, message} from 'antd';
import {observer} from 'mobx-react-lite';
import {useHistory} from 'react-router-dom';
import './index.less';
import {useLogin} from '@/App/httpClientRequest/useApi';
import {setToken} from "@/App/utils/operatorLocalStorage";
import {useGlobalStore} from "@/App/store/GlobalStore";
import md5 from 'md5';

const Login = observer(props => {
  const globalStore = useGlobalStore();
  const router = useHistory();
  const {request: loginRequest, loading} = useLogin();

  // 
  const pwdValidate = (rule: any, value: string) => {
    const regular = /^[a-zA-Z0-9]{6,20}$/;
    if (!value) {
      return Promise.reject('');
    }
    if (regular.test(value)) {
      return Promise.resolve();
    } else {
      return Promise.reject(' 6-20 ');
    }
  };

  // 
  const loginSubmit = async (values: any) => {
    values.password = md5(values.password);
    const res = await loginRequest(values);
    const resData = res.data;
    if (resData.code !== '1') {
      message.error(resData.message);
    } else {
      setToken(`Bearer ${resData.token}`);
      globalStore.user.token = resData.token;
      router.push('/');
      message.success('');
    }
  };

  return (
    <div className="login-wrapper">
      <div className="login-box">
        <h1 className='welcome'></h1>
        <Form
          onFinish={loginSubmit}
        >
          <Form.Item
            name='username'
            rules={[
              {required: true, message: ''},
            ]
            }
          >
            <Input
              autoComplete='off'
              placeholder=''
            />
          </Form.Item>
          <Form.Item
            name='password'
            rules={[
              {validator: pwdValidate}
            ]}
          >
            <Input.Password
              placeholder=' 6-20 '
              autoComplete='off'
            />
          </Form.Item>
          <Form.Item>
            <Button
              loading={loading}
              className='login-btn'
              type='primary'
              htmlType='submit'></Button>
          </Form.Item>
        </Form>
        <div className="to-register">
          <span onClick={() => {
            router.push('/register')
          }}></span>
        </div>
      </div>
    </div>
  );
});

export default Login;
